<template>
	<view>
		<navbar v-model="screenHeight" :iSimmersive="false" :placeholder="true" title="我的海报"></navbar>
		<view v-if="!isLoading && isError == false" class="poster_page" :style="{'min-height': (screenHeight-40)+'px'}">
			<canvas canvas-id="poster" class="poster_canvas"></canvas>
			<swiper class="poster_swiper" previous-margin="110rpx" circular :current="swiperIndex" next-margin="110rpx" @change="onSwiperChange">
				<swiper-item v-for="(item, index) of promoteBgImgs" :key="index">
					<view class="poster_image" :class="{ active: swiperIndex == index }">
						<image class="poster_bg_image" :src="item"></image>
						<image class="poster_code_image" :src="promoteCodeImg"></image>
					</view>
				</swiper-item>
			</swiper>
			
			<view class="bottom">
				<view @click="onSaveImg">保存海报</view>
				<!-- #ifdef APP-PLUS -->
				<view @click="onAppShare">一键邀请</view>
				<!-- #endif -->
			</view>
		</view>
		<loading v-if="isLoading"></loading>
		<empty-box type="service" v-if="isError && isLoading == false" @onEvents="onJump"></empty-box>
	</view>
</template>

<script>
import loading from '../../components/tool/loading';
import config from "@/config";
import navbar from "@/components/navbar/navbar";
export default {
	components: {
		loading,navbar
	},
	data() {
		return {
			screenHeight: 0,
			isLoading: true,
			isError: false,
			promoteBgImgs: [],
			promoteCodeImg: "", // qrcode
			swiperIndex: 0,
			posterImgs: [],
			shareInfo: {}
		};
	},
	onLoad(e) {
		this.onLoadData();
	},
	//方法
	methods: {
		onLoadData(){
			let agent = this.$utils.platformAgent();
			this.$http.getShareData({
				type: agent.type
			}).then((res)=>{
				if(res.status){
					this.promoteBgImgs = res.data.images;
					this.promoteCodeImg = res.data.qrcode;
					this.shareInfo = {
						shareContent: res.data.content,
						shareImg: res.data.images[0],
						shareTitle: res.data.title,
						shareUrl: res.data.url
					};
					this.isLoading = false;
					this.isError = false;
				}else{
					this.isLoading = false;
					this.isError = true;
				} 
			}).catch((err)=>{
				this.isLoading = false;
				this.isError = true;
			});
		},
		onJump(){
			this.onLoadData();
		},
		// 轮播图变化
		onSwiperChange(e) {
			this.swiperIndex = e.detail.current;
		},
		// 创建海报
		createPoster() {
			return new Promise((resolve, reject) => {
				if (this.posterImgs[this.swiperIndex]) {
					resolve(this.posterImgs[this.swiperIndex]);
				} else {
					uni.showLoading({
						title: '海报生成中'
					});
					const ctx = uni.createCanvasContext('poster');
					ctx.fillRect(0, 0, 375, 667);
					uni.downloadFile({
						url: this.promoteBgImgs[this.swiperIndex],
						success: res => {
							if (res.statusCode === 200) {
								ctx.drawImage(res.tempFilePath, 0, 0, 375, 667);
								uni.downloadFile({
									url: this.promoteCodeImg,
									success: res => {
										if (res.statusCode === 200) {
											ctx.drawImage(res.tempFilePath, 137.45, 504, 100.1, 100.1);
											ctx.draw(true, () => {
												// canvas画布转成图片并返回图片地址
												uni.canvasToTempFilePath({
													canvasId: 'poster',
													width: 375,
													height: 667,
													success: res => {
														if(this.posterImgs[this.swiperIndex]){
															this.posterImgs[this.swiperIndex].temporary = res.tempFilePath;
														}else{
															this.posterImgs[this.swiperIndex] = {};
															this.posterImgs[this.swiperIndex].temporary = res.tempFilePath;
														}
														resolve(res.tempFilePath);
													},
													fail: () => {
														uni.hideLoading();
														reject();
													}
												});
											});
										} else {
											uni.hideLoading();
											uni.showToast({
												title: '海报制作失败，图片下载失败',
												icon: 'none'
											});
										}
									},
									fail: err => {
										uni.hideLoading();
										uni.showToast({
											title: '海报制作失败，图片下载失败',
											icon: 'none'
										});
									}
								});
							} else {
								uni.hideLoading();
								uni.showToast({
									title: '海报制作失败，图片下载失败',
									icon: 'none'
								});
							}
						},
						fail: err => {
							uni.hideLoading();
							uni.showToast({
								title: '海报制作失败，图片下载失败',
								icon: 'none'
							});
						}
					});
				}
			});
		},
		// 保存图片
		async onSaveImg() {
			let imgUrl = "";
			if(this.posterImgs[this.swiperIndex] && this.posterImgs[this.swiperIndex].temporary){
				imgUrl = await this.posterImgs[this.swiperIndex].temporary;
			}else{
				imgUrl = await this.createPoster();
			}
			
			uni.showLoading({
				title: '海报下载中'
			});
			uni.saveImageToPhotosAlbum({
				filePath: imgUrl,
				success: () => {
					uni.hideLoading();
					uni.showToast({
						title: '保存成功'
					});
				}
			});
		},
		uploadFile(file){
			let users = this.$storage.getJson("users");
			let that = this;
			return new Promise((resolve, reject)=>{
				uni.uploadFile({
				    url: config.uni_app_web_api_url + '' + '/share/upload',
				    filePath: file,
				    name: 'file',
				    header: { "Auth-Token" : users.token },
				    success: (uploadFileRes) => {
						let res = JSON.parse(uploadFileRes.data);
						console.log("upload",res);
						if(res.status){
							resolve(res.data);
						}else{
							reject(res.info);
						}
				    }
				});
			});
		},
		async onAppShare() {
			let imgUrl = "";
			try{
				if(this.posterImgs[this.swiperIndex] && this.posterImgs[this.swiperIndex].url){
					imgUrl = this.posterImgs[this.swiperIndex].url;
				} else if(this.posterImgs[this.swiperIndex] && this.posterImgs[this.swiperIndex].temporary){
					let imgUrl = await this.uploadFile(this.posterImgs[this.swiperIndex].temporary);
					this.posterImgs[this.swiperIndex].url = imgUrl;
				}else{ 
					let data = await this.createPoster();
					let imgUrl = await this.uploadFile(data);
					this.posterImgs[this.swiperIndex].url = imgUrl;
				}
			}catch(e){
				console.log("error",e);
				uni.hideLoading();
				uni.showToast({
					title: '转发海报失败，请稍后在试',
					icon: 'none'
				});
				return ;
			}
			
			uni.hideLoading();
			uni.share({
				provider: 'weixin',
				scene: 'WXSceneSession',
				type: 0,
				title: this.shareInfo.shareTitle,
				href: this.shareInfo.shareUrl,
				summary: this.shareInfo.shareContent,
				imageUrl: this.shareInfo.shareImg,
				success: function(res) {
					console.log('success:' + JSON.stringify(res));
				},
				fail: function(err) {
					console.log('fail:' + JSON.stringify(err));
				}
			});
		}
	}
};
</script>
<style lang="scss" scoped>
.poster_page {
	min-height: calc(100vh - 150px);
	background-color: #fff;
	/* #ifdef H5 */
	padding-top: 80rpx;
	/* #endif */
	/* #ifdef APP-PLUS || MP */
	display: flex;
	align-items: center;
	/* #endif */
}
.poster_canvas {
	width: 750rpx;
	height: 1334rpx;
	position: fixed;
	top: -10000rpx;
	left: 0rpx;
}
.poster_swiper {
	height: 934rpx;
	width: 100%;
	swiper-item {
		box-sizing: border-box;
		display: flex;
		align-items: center;
		.poster_image {
			width: 100%;
			height: 100%;
			transform: scale(0.78);
			transition: all 0.4s;
			position: relative;
			display: flex;
			align-items: flex-end;
			overflow: hidden;
			&.active {
				transform: scale(1);
			}
			.poster_bg_image {
				width: 100%;
				height: 100%;
				border-radius: 15rpx;
			}
			.poster_code_image {
				position: absolute;
				bottom: 100rpx;
				width: 140rpx;
				height: 140rpx;
				left: 50%;
				transform: translateX(-50%);
			}
		}
	}
}
.bottom{
	padding: 20rpx 0;
	width: 100%;
	display: flex;
	justify-content: space-around;
	position: fixed;
	bottom: 0;
	background-color:#ffffff;
	view:first-child{
		padding: 20rpx 60rpx;
		background:#FF8A00;
		border-radius:44rpx;
		font-size:30rpx;
		color:#FFFFFF;
	}
	view:last-child{
		padding: 20rpx 60rpx;
		background:#FF5256;
		border-radius:44rpx;
		font-size:30rpx;
		color:#FFFFFF;
	}
}
</style>
